.main {
	color: $primaryColor;
	@include background(url("../images/main-block/main-block-bg.jpg"));
	background-position: 63%;

	@media only screen and (min-width: 768px) {
		background-size: 230%;
		background-position: 56%;
	}

	@media only screen and (min-width: 1280px) {
		background-size: 140%;
		background-position: 50%;
	}

	@media only screen and (min-width: 1440px) {
		background-size: 134%;

	}

	@media only screen and (min-width: 1440px) {
		background-position: center;
		background-size: cover;
	}



	.container {
		@extend %container;
	}

	// .main__inner
	&__inner {
		@include flex(center, $flex-direction: column);
		height: 655px;

		@include media(sm) {
			height: 840px;
		}

	}
		// .main__title
		&__title {
			@include font(32px, 39px, 700);
			color: $primaryColor;
			max-width: 247px;
			margin-bottom: 35px;

			@media only screen and (min-width: 768px) {
				max-width: 600px;
			}

			span {
				color: $accentColor;
			}
		}

		// .main__price-box
		&__price-box {
			@include wh-px(200, 86);
			@include background(url("../images/main-block/main-block-price-box.svg"));
			@include flex(space-around);
			margin-bottom: 10px;
			box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
			border-radius: 5px;
		}

		// .main__wrapper
		&__wrapper {
			@include flex(space-between, $flex-direction: column);
			padding: 8px 0 18px 16px;
		}

		// .main__price-label
		&__price-label {
			@include font(18px, inherit);

		}

		&__price {
			@include font(30px, inherit, 800);
		}

		// .main__percent
		&__percent {
			@include wh (77px);
			transform: translateY(4px);
		}

		// .main__btn
		&__btn {
			@include wh(100%, 68px);
			max-width: 275px;
			@include border(3px, solid, $primaryColor);
			border-radius: 7px;
			@include font(16px, 20px, 900, center);
			filter: drop-shadow(0px 4px 70px rgba(255, 255, 255, 0.7)),
				drop-shadow(0px 4px 4px rgba(255, 255, 255, 0.25));
			margin-bottom: 15px;
		}
	}
